.author-titles-list {
  width: 100%; }
  .author-titles-list .name-seccion {
    font-size: 1.1em;
    font-family: 'Adelle Sans W01 Bold'; }
  .author-titles-list ul {
    margin: 0;
    list-style: none; }
    .author-titles-list ul li {
      margin: 0;
      padding: 1.5rem 0; }
      .author-titles-list ul li .name-title {
        font-size: 1.1em; }
        .author-titles-list ul li .name-title a {
          color: #405063; }
      .author-titles-list ul li .name-editorial {
        color: #A9B4C0; }
        .author-titles-list ul li .name-editorial a {
          color: #A9B4C0;
          line-height: 35px; }
      .author-titles-list ul li div span {
        border: 1px solid #E8E8E8;
        border-radius: 5px;
        height: 34px;
        line-height: 34px;
        display: inline-table;
        padding: 0 1rem;
        font-size: 0.8em; }
        .author-titles-list ul li div span:nth-child(1) {
          color: #A7DD70; }
        .author-titles-list ul li div span:nth-child(2) {
          color: #A9B4C0; }
        .author-titles-list ul li div span:nth-child(3) {
          color: #A7DD70;
          text-transform: uppercase; }

@media (min-width: 500px) {
  .author-titles-list ul li div span {
    font-size: 1em; } }



